import { Control, Controller, FieldError } from 'react-hook-form';
import {
  AutocompleteRenderOptionState,
  InputLabelProps,
  TextField,
  Autocomplete as MuiAutocomplete
} from '@material-ui/core';
import { FieldRules } from 'types/FieldRules';
import { FieldName } from 'react-hook-form/dist/types/form';
import { css } from 'emotion';
import React from 'react';

export interface SelectOption<R> {
  label: string;
  value: R;
}

interface Props<T,R> {
  name: keyof T;
  control: Control<T>;
  error?: FieldError;
  rules?: FieldRules;
  label?: string;
  options: Array<SelectOption<R>>;
  placeholder?: string;
  getOptionLabel?: (option: SelectOption<R>) => string;
  renderOption?: (
    props: React.HTMLAttributes<HTMLLIElement>,
    option: SelectOption<R>,
    state: AutocompleteRenderOptionState
  ) => React.ReactNode;
  autoFocus?: boolean;
  creatable?: boolean;
  InputLabelProps?: Partial<InputLabelProps>;
  disableClearable?: boolean | undefined;
}

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

const AutoCompleteField = <T extends Record<string, any>, R extends any>(props: Props<T,R>) => {
  const {
    name,
    control,
    error,
    rules,
    label,
    placeholder,
    creatable,
    getOptionLabel = (option: SelectOption<R>) => option?.label || '',
    disableClearable,
    renderOption,
    autoFocus,
    options,
  } = props;

  return (
    <Controller
      control={control}
      name={name as FieldName<T>}
      rules={rules}
      render={({ onChange, onBlur, value, name }) => {
        // const selectedValueWithLabel = options.find(option => option?.value === value);

        return (
          <MuiAutocomplete
            id={name}
            autoHighlight
            // freeSolo={creatable}
            disableClearable={disableClearable}
            options={options}
            getOptionSelected={(option: SelectOption<R>, value: SelectOption<R>) => { 
              return value ? option.value === value.value : false;
            }}
            renderOption={renderOption}
            selectOnFocus={creatable}
            clearOnBlur={creatable}
            getOptionLabel={getOptionLabel}
            value={value}
            // value={selectedValueWithLabel}
            onChange={(evt, newValue) => onChange(newValue)}
            onBlur={onBlur}
            renderInput={({ InputLabelProps, InputProps, ...rest }) => 
              <TextField
                autoFocus={autoFocus}
                className={TextFieldStyle}
                label={label}
                error={!!error}
                InputLabelProps={{ shrink: true, ...InputLabelProps }}
                InputProps={{ placeholder: placeholder, ...InputProps }}
                helperText={ error?.message ?? '' }
                {...rest}
              />
            }
          />
        )
      }}
    />
  )
};

export default AutoCompleteField;
