import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, useMediaQuery, useTheme } from '@material-ui/core';
import AutoCompleteField, { SelectOption } from 'components/form/AutoCompleteField';
import TextField from 'components/form/TextField';
import { css } from 'emotion';
import React from 'react';
import { useForm } from 'react-hook-form';
import { bp } from 'theme';
import { AccountType, AccountTypeLabels } from 'types/AccountType';

const ContentStyle = css`
  ${bp.xs} {
    width: 100%
  }
  ${bp.xlMob} {
    min-width: 500px;
    width: unset
  }
`

interface NewAccountForm {
  Name: string;
  AccountType?: SelectOption<AccountType> | undefined;
}

const defaultValues: NewAccountForm = {
  Name: '',
}

const accountTypeOptions = [...AccountTypeLabels.entries()].map(([accountType, label]) => 
  ({ label: label, value: accountType })
);

export interface NewAccountDialogProps {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (newAccount: NewAccountForm) => void;
}

function NewAccountDialog({ isOpen, onClose, onSubmit }: NewAccountDialogProps) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xlMob'))

  const methods = useForm<NewAccountForm>({
    defaultValues,
    mode: 'onChange',
  });

  const {
    handleSubmit,
    control,
    getValues,
  } = methods;

  function onModalClose() {
    onClose();
  }

  function onModalOK() {
    onSubmit(getValues());
  }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        New Account
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <form onSubmit={handleSubmit(() => {})}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <AutoCompleteField
                name="AccountType"
                autoFocus
                control={control}
                label="Type"
                options={accountTypeOptions}
                rules={ { required: 'Required' } }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                name="Name"
                control={control}
                label="Name"
                rules={ { required: 'Required' } }
              />
            </Grid>
          </Grid>
        </form>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" onClick={onModalOK}>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default NewAccountDialog;
