import { Control, Controller, FieldError } from 'react-hook-form';
import MuiTextField from '@material-ui/core/TextField';
import { FieldName } from 'react-hook-form/dist/types/form';
import { FieldRules } from 'types/FieldRules';
import { css } from 'emotion';
import React from 'react';

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

interface Props<T> {
  id?: string;
  name: keyof T;
  control: Control<T>;
  label?: string;
  className?: string;
  error?: FieldError;
  rules?: FieldRules;
  placeholder?: string;
  multiline?: boolean;
  minRows?: number;
  // type?: 'text' | 'number' | 'password';
  disabled?: boolean;
  autoFocus?: boolean;
  transform?: (value: string) => any;
}

const TextField = <T extends object>(props: Props<T>) => {
  const {
    id,
    name,
    control,
    label,
    error,
    rules,
    multiline,
    minRows,
    // type,
    placeholder,
    disabled,
    autoFocus,
    transform
  } = props;

  return (
    <Controller
      control={control}
      name={name as FieldName<T>}
      rules={rules}
      render={({ onChange, onBlur, value, name }) => (
        <MuiTextField
          id={id || name}
          fullWidth
          autoFocus={autoFocus}
          className={TextFieldStyle}
          multiline={multiline}
          minRows={minRows}
          // type={ type }
          label={label}
          placeholder={placeholder}
          onChange={(event) => {
            if (transform) {
              onChange(transform(event.target.value));
            } else {
              onChange(event.target.value);
            }
          }}
          onBlur={onBlur}
          value={value}
          error={!!error}
          InputLabelProps={{ shrink: true }}
          helperText={error?.message ?? ''}
          disabled={disabled}
        />
      )}
    />
  );
};

export default TextField;
